import React from 'react';
import { Container } from '@material-ui/core';
import { Doughnut } from 'react-chartjs-2';
import { useFoodStateValue } from '../context/food-state';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers';

const MACRO_SECTION_DATA = [
  { title: 'Bílkoviny', type: 'protein', subSection: [] },
  { title: 'Sacharidy', type: 'carbohydrate', subSection: [] },
  {
    title: 'Tuky',
    type: 'totalFat',
    subSection: [
      { title: 'Nasycené mastné kyseliny', type: 'saturatedFat' },
      {
        title: 'Mononenasycené mastné kyseliny',
        type: 'monounsaturatedFat',
      },
      {
        title: 'Polynenasycené mastné kyseliny',
        type: 'polyunsaturatedFat',
      },
      {
        title: 'Trans mastné kyseliny',
        type: 'transFat',
      },
    ],
  },
];

export const RecipeNutrition = () => {
  const { foodState } = useFoodStateValue();
  const { calculateFoodValue, isFoodStateEmpty } = useFoodStateHelpers();

  const data = {
    datasets: [
      {
        data: MACRO_SECTION_DATA.map(({ type }) =>
          calculateFoodValue(type) == 0 ? 0.1 : calculateFoodValue(type)
        ),
        backgroundColor: [
          'rgb(255, 99, 132)',
          'rgb(54, 162, 235)',
          'rgb(255, 206, 86)',
        ],
      },
    ],

    labels: ['Bílkoviny', 'Sacharidy', 'Tuky'],
  };

  return (
    <Container className='recipe-nutrition'>
      <div className='recipe-nutrition__dougnut'>
        <Doughnut
          data={data}
          width={240}
          height={240}
          options={{ maintainAspectRatio: false }}
        />
      </div>
      <div className='recipe-nutrition__data'>
        <div className='data-section'>
          <h3 className='data-section__title'>Kalorie</h3>
          <p>
            Celkem:{' '}
            <span className='highlight--bold'>
              {calculateFoodValue('calories')} kcal
            </span>{' '}
          </p>
        </div>
        {
          //! Macro
          MACRO_SECTION_DATA.map(({ title, type, subSection }) => (
            <div className='data-section'>
              <h3 className='data-section__title'>{title}</h3>
              <div className='data-section__data'>
                <p>
                  Celkem:{' '}
                  <span className='highlight--bold'>
                    {calculateFoodValue(type)} g
                  </span>{' '}
                </p>
                {subSection.map(({ title, type }) => (
                  <p>
                    {title}:{' '}
                    <span className='highlight--bold'>
                      {calculateFoodValue(type)} g
                    </span>
                  </p>
                ))}
              </div>
            </div>
          ))
        }
        {/* {
          //! Macro
          MACRO_SECTION_DATA.map(({ title, type, subSection }) => (
            <div className='data-section'>
              <h3 className='data-section__title'>{title}</h3>
              <p>celkem: {calculateFoodValue(type)}</p>
            </div>
          ))
        } */}
      </div>
    </Container>
  );
};
